body{
    margin:0px;
    padding: 0px;
    
    background-color: #ffffff;
    font-family: 'Montserrat', sans-serif;
  }
  *{
    box-sizing: border-box;
  }
  ul{
    list-style: none;
  }
  a{
    text-decoration: none;
  }
  button{
    outline: none;
    border: 1px solid #ccc;
  }
  input{
    outline: none;
    border: 1px solid #ccc;;
  }
  .logo img{
    width: 90px;
  }
  #main{
  
    width:100%;
    height:768px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('./images/background.png');
  }
  nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width:100%;
  z-index: 1;
    background-color: transparent;
    transition: 0.6s ease-in-out;
  }
  nav.active{
  box-shadow: 5px 10px 30px hsl(162, 50%, 50%);
  background-color: #ffffff;
  }
  nav ul{
  display: flex;
  }
  .active{
    background-color:hsl(162, 50%, 50%);
    color:#ffffff;
  box-shadow: 5px 10px 30px rgba(189, 198, 64, 0.411);
  transition: all ease 0.2s;
  }
  nav ul li a{
    font-family: calibri;
  height:40px;
  line-height: 43px;
  margin: 3px;
  padding: 0px 22px;
  display: flex;
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 500;
  color:#000000;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
  }
  nav ul li a:hover{
  background-color:hsl(162, 50%, 50%);
    color:#ffffff;
  box-shadow: 5px 10px 30px rgba(2, 238, 255, 0.336);
  transition: all ease 0.2s;
  }
  nav .menu-btn,
  .menu-icon{
    display:none;
  }
  .name{
  font-family: 'Montserrat';
  width: 500px;
  position: absolute;
    left: 22%;
  top: 50%;
  transform: translate(-22%,-50%);
  }
  .name span{
    color: hsl(162, 50%, 50%);
  }
  .name .details{
    color: #969696;
  }
  .name h1{
  font-family:'Montserrat';
  font-size: 40px;
  margin:0px;
  letter-spacing: 2px;
  color:#312f2f;
  }
  .header-btns{
    display: flex;
    margin-top: 40px;
  }
  .cv-btn{
  width:110px;
  height: 40px;
  display: flex;
  justify-content: center;
    align-items: center;
    background-color:hsl(162, 50%, 50%);
    box-shadow: 5px 10px 30px hsl(162, 50%, 50%);
    border-radius: 10px;
  color:#ffffff;
  transition: transform 350ms;
  }
  .cv-btn:hover{
  background-color:hsl(162, 50%, 50%);
    transition: all ease 5s;
    color: #ffffff;
    transform: translateX(-30px);
  }
  
  .f-heading h1{
    color:#FFFFFF;
    font-size: 3rem;
    margin: 0px;
    padding: 0px;
  
  }
  .f-heading p{
    color:rgba(247, 247, 247, 0.678);
    font-size: 1rem;
    margin: 0px;
    padding: 0px;
  }
  .f-heading{
    text-align: center;
    margin-top: 60px;
    margin-bottom: -80px;
  }
  #about{
    margin-top:100px ;
  width:100%;
  height: 120vh;
  background-color:#f3fdff;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 5% 0px 5%;
  position: relative;
  }
  
  .about-text{
    width: 45%;
    color: #fff;
    list-style: circle;
  }
  .about-text h2{
  font-size: 2rem;
  color:hsl(162, 50%, 50%);
    font-weight: 500;
    margin: 0px;
  padding: 0px;
  }
  .about-text p{
    width: 80%;
  font-size: 1.2rem;
  color:#272727;
  
  }
  .about-text button{
    margin-top: 20px;
  width: 140px;
  height: 45px;
  border-radius: 10px;
  border: none;
    outline: none;
    color: #ffffff;
  background-color:hsl(162, 50%, 50%);
  }
  .about-text button:hover{
  background-color: hsl(162, 50%, 50%);
    transition: all ease 0.3s;
    color: #ffffff;
  }
  .about-image{
    width: 50%;
  }
  .about-image img{
  width: 600px;
  }
  
  #features{
    width:100%;
    height:100vh;
    box-sizing: border-box;
    font-family: calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
    background-color: #ffffff;
  }
  .a-container{
    display: flex;
    justify-content: center;
    align-content: center;
  
  
  }
  .a-box{
    background-color: #ffffff;
    width:300px;
    height: 450px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 60px 10px rgba(0,0,0,0.1);
    transition: 5s ease-in-out;
    position: relative;
  }
  .a-b-img{
    width:60%;
    height: 40%;
    margin-left: 50px;
  
  }
  .a-b-img img{
    padding: 7px;
    width:100%;
    height: 100%;
  
  }
  .a-box:hover{
    box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
  }
  #presentaion{
    padding-top: 30px;
    width:100%;
    height:768px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(images/presentation.png);
    text-align: center;
  }
  #presentaion h1{
  font-size: 2rem;
  }
  #services{
    width:100%;
    height:100vh;
    box-sizing: border-box;
    font-family: calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
  }
  
  .s-heading h1{
    color:#FFFFFF;
    font-size: 3rem;
    margin: 0px;
    padding: 0px;
  
  }
  .s-heading p{
    color:rgba(247, 247, 247, 0.678);
    font-size: 1rem;
    margin: 0px;
    padding: 0px;
  }
  .s-heading{
    text-align: center;
    margin: 20px 0px;
  }
  .s-box{
    background-color: #ffffff;
    width:350px;
    height: 470px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
    position: relative;
  }
  .b-container{
    display: flex;
    justify-content: center;
    align-content: center;
  
  
  }
  .s-b-img{
    width: 100%;
    height: 50%;
  
  }
  .s-b-img img{
    width:100%;
    height: 100%;
  
  }
  .s-b-text{
    width: 100%;
    height: 40%;
    background-color:#ffffff;
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
  }
  .s-b-text p{
    margin: 0px;
    color:#1fa0a0a6;
    font-size: 1.1rem;
    font-family: calibri;
    display: block;
    display: -webkit-box;
    max-width: 80%;
    margin-top: 25px;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .s-b-text p:hover{
    color:#23bbc0;
  }
  .s-b-text a{
    margin-top:15px ;
  }
  .s-type{
    font-family: calibri;
    color:#000266;
    background-color: #ffffff;
    padding: 10px 15px;
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 18px;
    border-radius: 0px 0px 10px 10px;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.2);
  }
  
  
